.base_kn{
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.base_kn h1{
    font-family: "TTT";
    font-size: 40px;
    font-weight: 100;
    align-self: flex-start;
    padding: 60px 70px;
}

.base_kn_blocks{
    width: 95%;
    height: 540px;
    background-color: #3E83BA;
    display: flex;
    border-radius: 16px;
    align-items: center;
    justify-content: space-around;
}
.base_kn_blocks img{
    width: 576px;
    height: 408px;
}
.base_four_blocks{
    width: 55%;
    display: flex;
    gap: 15px;
}
.base_two_blocks{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.block{
    width: 100%;
    height: 216px;
    border-radius: 16px;
    background-color: white;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 15px;
    transition: 0.5s;
    cursor: pointer;
}
.bl_line{
    background-color: #EAEAEA;
    height: 2px;
    width: 100%;
    align-self: center;
}
.block h2{
    font-size: 22px;
    font-family: 'TTT';
}
.block p{
    font-size: 14px;
    font-family: 'TTT';
}
.block a{
    color: black;
    font-size: 13px;
    font-family: 'TTT';
}
.block:hover{
    transform: scale(1.02);
    transition: 0.5s;
    border: 1px solid black;
}
@media screen and (max-width: 500px) and (min-width: 360px){
    .base_kn {
      padding: 10px 0;
    }
    .base_kn h1{
        width: 100%;
        padding: 20px 0;
        text-align: center;
    }
    .base_kn_blocks{
        padding: 20px 0;
        width: 100%;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: start;
    }
    .base_four_blocks{
        display: flex;
        flex-direction: column;
        width: 95%;
    }
    .base_kn_blocks img{
        display: none;
    }
  }