.don_card{
    position: relative;
    button{
        width: 90%;
        padding: 10px 0;
        color: #678CD3;
        background-color: #F1F7FF;
        border-radius: 8px;
        border: 2px solid #678CD3;
        font-family: 'TTT';
        font-size: 20px;
        font-weight: 500;
        cursor: pointer;
        transition: 0.5s;
    }
    button:hover{
        background-color: #678CD3;
        color:#F1F7FF;
        transition: 0.5s;
    }
}
.card_procent{
    position: absolute;
    top: 0px;
    left: 0px;
    border: 2px solid #678CD3;
    border-radius: 8px;
    padding: 10px;
    background-color: #F1F7FF;
    p{
        color: #678CD3;
        font-size: 30px;
        font-family: 'TTT';
        font-weight: 500;
    }
}
@media screen and (max-width: 500px) and (min-width: 360px){
    .links_help{
        flex-direction: column;
        a{
            padding: 20px 20px;
        }
    }
    .razdel{
        h1{
            font-size: 40px;
        }
    }
    .info_block_parts{
        flex-direction: column;
    }
    .info_block1{
        flex-direction: column;
        img{
            width: 90% !important;
            height: 100% !important;
        }
        .info_text{
            width: 90%;
        }
    }
  }
  